import Interface from '../components/interface'
import React from "react";

class ImageWithLoader extends React.Component {
  constructor(props) {
    super(props);
    this.state = { imgStatus: "" };
  }
  
  imgInit() {
    this.setState({ imgStatus: "show" });
  }

  imgLoaded() {
    this.setState({ imgStatus: "loaded" });
  }

  imgError() {
    this.setState({ imgStatus: "error" });
  }

  render() {
    return (
      <a href={this.props.href} target={this.props.target}>
        <featimg>
          <imgprogress className={this.state.imgStatus}><spinner></spinner></imgprogress>
          <img
            src={this.props.src}
            alt={this.props.alt || ''}
            onLoad={this.imgLoaded.bind(this)}
            onError={this.imgError.bind(this)}
            onLoadStart={this.imgInit.bind(this)}
          />
          <text>{this.props.alt || ''}</text>
        </featimg>
        <style jsx>{`
          featimg {
            background-image: url(${this.props.src});
            background-repeat: no-repeat;
            background-position: center;
            background-color: #f4f4f4;
            background-size: cover;
            
            border: 2px solid #f4f4f4;
            margin-bottom: 50px;
            padding-top: 180px;
            display: block;
            float: left;
            width: 49%;
          }
          a:nth-child(even) featimg {
            float: right;
          }
          featimg:hover {
            border: 2px solid #8500FF;
          }
          featimg imgprogress.show {
            position: absolute;
            margin-top: -125px;
            margin-left: 130px;
          }
          featimg imgprogress.show spinner {
            border: solid 2px transparent;
            border-bottom-color: #6055fb;
            border-right-color: #8103f4;
            border-left-color: #8103f4;
            border-top-color: #6055fb;
            box-sizing: border-box;
            border-radius: 50%;
            display: block;
            height: 70px;
            width: 70px;
            -webkit-animation: spinner 400ms linear infinite;
            animation: spinner 400ms linear infinite;
            box-shadow: 0px 1px 7px 1px #8103f4;
          }
          
          @-webkit-keyframes spinner {
            0%   { -webkit-transform: rotate(0deg); }
            100% { -webkit-transform: rotate(360deg); }
          }
          @keyframes spinner {
            0%   { transform: rotate(0deg); }
            100% { transform: rotate(360deg); }
          }
          
          featimg img {
            display: none;
          }
          featimg text {
            border: 2px solid #f4f4f4;
            width: calc(100% + 4px);
            text-align: center;
            position: relative;
            background: white;
            font-weight: 500;
            display: block;
            bottom: -35px;
            padding: 5px;
            left: -2px;
          }
          featimg:hover text {
            border: 2px solid #8500FF;
            color: #8500FF;
          }
          
          @media (max-width: 768px) {
            featimg {
              width: 100%;
            }
          }
        `}</style>
      </a>
    );
  }
}

